body {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a:hover {
  opacity: 0.6;
  transition: .3s ease;
  text-decoration: underline;
}
#root {
  background-color: #303030;
}

.App {
  color: white;
  font-family: source-code-pro, monospace;
  min-height: 100vh;
  text-align: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto 5fr auto;
  grid-template-areas:
  "header"
  "nav"
  "main"
  "main"
  "main"
  "footer"
}

.App a {
  color: inherit;
  outline: none;
}

.App-logo {
  margin: 2em auto auto auto;
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 12vmin;
}

#App-header {
  display: inline;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  grid-area: header;
}

#nav {
  grid-area: nav;
}

#content-wrapper {
  flex: 1 0 auto;  
  margin-top: 5vh;
  align-self: start;
  grid-area: main;
}

#footer {
  grid-area: footer;
}

.container { 
  font-family: source-code-pro, monospace;
}

.container .title {
  font-weight: bold;
  font-size: 2em;
}

.container p {
  margin-top: 1.5em;
}

.title { 
  -webkit-user-select: none; 
     -moz-user-select: none; 
      -ms-user-select: none; 
          user-select: none;
}

.ui.divider { 
  margin: 5%!important; 
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#navList {
    margin: 3%;
    padding-left: 5px;
    padding-right: 5px;
    text-align: center;
    list-style: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.navItem {
    display: inline;
    font-size: 1.3em;
    margin: auto;
}

.navItem:not(:last-child):after {
    content:' | ';
}
#footer {
    flex-shrink: 0;
    margin-top: 8em;
    width: 100%;
    height: 2em;
    text-align: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
#report {
    margin-top: 2em;
    text-decoration: underline;
}
